<template>
  <div v-show="isShowAlert" class="alert" :class="isCloseClass ? 'close' : ''">
    <i class="el-icon-warning"></i>
    <div class="alert__content">
      <span class="alert__title">
        提示:
        文件的加密密码已显示在加密文件的文件名中,请务必保存好您的密码，一旦丢失，您将无法解密您的文件
      </span>
      <p
        v-for="(decItem, decIndex) in decArr"
        :key="decIndex"
        class="alert__description"
      >
        {{ decItem }}
      </p>
      <i @click="clearAlert" class="alert__closebtn">我已知晓</i>
    </div>
  </div>
</template>

<script>
let { version } = require("./../../package.json");
version = `V${version || 1.0}`;
export default {
  data() {
    return {
      isShowAlert: true,
      isCloseClass: false,
      decArr: []
    };
  },
  methods: {
    clearAlert() {
      this.isCloseClass = true;
      setTimeout(() => {
        this.isShowAlert = false;
      }, 300);
      localStorage.setItem(version, true);
    }
  },
  mounted() {
    const localVersionData = localStorage.getItem(version);
    if (!localVersionData) {
      this.isShowAlert = true;
    } else {
      this.isShowAlert = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.alert {
  background-color: #fdf6ec;
  color: #e6a23c;
  width: 100%;
  padding: 8px 16px;
  margin: 0;
  box-sizing: border-box;
  border-radius: 4px;
  position: relative;
  opacity: 1;
  display: flex;
  align-items: center;
  transition: opacity 0.2s;
  &.close {
    opacity: 0;
  }
  .el-icon-warning {
    font-size: 28px;
    width: 28px;
  }
  .alert__content {
    display: table-cell;
    padding: 0 8px;
    width: calc(100vw - 120px);
    .alert__title {
      font-weight: 700;
      font-size: 13px;
      line-height: 18px;
    }
    .alert__description {
      font-size: 12px;
      margin: 5px 0 0;
    }
  }

  .alert__closebtn {
    font-style: initial;
    font-size: 12px;
    opacity: 1;
    position: absolute;
    top: 12px;
    right: 15px;
    cursor: pointer;
    color: #999;
  }
}
</style>
